import React, { useState, useEffect, useCallback } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
import style from "./style.css";
import { Button } from "@mui/material";
import { Form, Container, Row, Col, Modal } from "react-bootstrap";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import XLSX from "xlsx";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";
import Dialog from "@mui/material/Dialog";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

function UpdateProjectTemplateBody(props) {
  let [item, setItem] = useState([]);
  let [items, setItems] = useState([]);
  let [show, setShow] = useState(false);
  let [project, setProject] = useState("");
  let [subProject, setSubProject] = useState("");
  let [name, setName] = useState("");
  let [open, setOpen] = useState(false);
  let [alart, setAlart] = useState("");
  let [severity, setSeverity] = useState("");

  let [openDialog, setOpenDialog] = useState(false);
  let [form, setForm] = useState({});
  let [errors, setErrors] = useState({});
  let [users, setUsers] = useState([]);

  function modalClose() {
    setShow(false);
  }

  const handleCloseLoading = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  let setField = (field, value) => {
    setForm({
      ...form,
      [field]: value,
    });
    if (!!errors[field])
      setErrors({
        ...errors,
        [field]: null,
      });
  };

  const findFormErrors = () => {
    const { subProject, project } = form;
    const newErrors = {};
    // name errors
    if (!subProject || subProject === "")
      newErrors.subProject = "cannot be blank!";
    if (!project || project === "") newErrors.project = "cannot be blank!";
    // console.log(headersConfig);
    // console.log(newErrors)
    return newErrors;
  };

  const onSubmit = (e) => {
    e.preventDefault();
    setOpenDialog(true);
    const newErrors = findFormErrors();
    // Conditional logic:
    if (Object.keys(newErrors).length > 0) {
      // We got errors!
      setErrors(newErrors);
      setOpenDialog(false);
    } else {
      const taskObject = {
        projectID: form.projectID,
        assignedProjectManager: form.assignedProjectManager,
        taskStatus: "Project template created",
      };

      // console.log(taskObject)

      axiosInstance
        .put(`projonline/updateprojecttemp/${form._id}`, form, {
          headers: {
            token:
              "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
          },
        })
        .then((res) => {
          // console.log(res.data.msg)
          setAlart(res.data.msg);
          setSeverity(res.data.variant);
          setOpen(true);
          setForm({
            _id: "",
            project: "",
            subProject: "",
            projectID: "",
            assignedProjectManager: "",
          });
          getData();
          setShow(false);
          setOpenDialog(false);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  };

  const toEdit = (data) => {
    setShow(true);
    setForm({
      _id: data._id,
      project: data.project,
      subProject: data.subProject,
      projectID: data.projectID,
      assignedProjectManager: data.assignedProjectManager?._id,
    });
  };

  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  useEffect(() => {
    getData();
  }, []);

  const getData = (event, cellValues) => {
    setOpenDialog(true);
    axiosInstance
      .get("projonline/project", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setItems(res.data.projTemplates);
        setOpenDialog(false);
        // console.log("----------");
        // console.log(res.data.projTemplates);
        // console.log("----------");
        return;
      })
      .catch((err) => {
        console.log(err);
      });
    axiosInstance
      .get("projonline/mobitelusers", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        // const result =
        // console.log(res.data)
        setUsers(res.data);

        return;
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const downloadHandover = (data) => {
    //Create Downloadable excel file
    var workbook = XLSX.utils.book_new();

    //get selected projects
    var worksheetData = [];

    // console.log(data)
    worksheetData.unshift(data.properties);

    var ws = XLSX.utils.aoa_to_sheet(worksheetData);
    XLSX.utils.book_append_sheet(workbook, ws, "Project Template Properties");
    XLSX.writeFile(workbook, "Project Template.xlsx");
  };

  const columns = [
    { field: "projectID", headerName: "Project ID", width: 250 },
    { field: "project", headerName: "Project", width: 200 },
    { field: "subProject", headerName: "Sub Project", width: 250 },
    {
      field: "name",
      headerName: "Assigned Project Manager",
      valueGetter: (params) => params.row.assignedProjectManager?.name,
      width: 250,
    },
    {
      field: "To Edit",
      width: 150,
      renderCell: (cellValues) => {
        return (
          <Button
            variant="contained"
            color="primary"
            onClick={(event) => toEdit(cellValues.row)}
          >
            To Edit
          </Button>
        );
      },
    },
    {
      field: "Download",
      width: 150,
      renderCell: (cellValues) => {
        return (
          <Button
            variant="contained"
            color="primary"
            onClick={(event) => downloadHandover(cellValues.row)}
          >
            Download
          </Button>
        );
      },
    },
  ];

  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        <div className="content-header">
          <section className="content-header">
            <div className="container-fluid">
              <div>
                <div>
                  <Container>
                    <Row>
                      <Col xs={4}>
                        <h1>All Project Templates</h1>
                      </Col>

                      <Col xs={2}></Col>
                      <Col>
                        <div className="vr" />
                      </Col>
                      <Col>
                        <Button
                          variant="contained"
                          color="primary"
                          onClick={(e) =>
                            props.history.push({
                              pathname: "/CreateProjectTemplate",
                            })
                          }
                        >
                          Add a new project template
                        </Button>
                      </Col>
                    </Row>
                  </Container>
                </div>
              </div>
            </div>
            {/* /.container-fluid */}
          </section>
        </div>

        <div style={{ height: 610, width: "100%" }}>
          <DataGrid
            getRowId={(item) => item._id}
            rows={items}
            columns={columns}
            pageSize={15}
            rowsPerPageOptions={[15]}
            rowHeight={30}
            headerHeight={40}
            components={{ Toolbar: GridToolbar }}
          />
        </div>
      </div>
      <Modal show={show} onHide={modalClose} animation={false}>
        <Modal.Header closeButton>
          <Modal.Title>Edit Project Template</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={onSubmit}>
            <Row className="mb-3">
              <Form.Group as={Col}>
                <Form.Label>Project ID</Form.Label>
                <Form.Control
                  disabled
                  onChange={(e) => setField("projectID", e.target.value)}
                  value={form.projectID}
                  className="col-md-9"
                  type="text"
                  placeholder="Enter Project Name"
                  isInvalid={!!errors.projectID}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.projectID}
                </Form.Control.Feedback>
              </Form.Group>

              <Form.Group as={Col}>
                <Form.Label>Project Manager</Form.Label>
                <Form.Control
                  onChange={(e) =>
                    setField("assignedProjectManager", e.target.value)
                  }
                  value={form.assignedProjectManager}
                  className="col-md-9"
                  as="select"
                  defaultValue="Choose..."
                  isInvalid={!!errors.assignedProjectManager}
                >
                  <option>Choose...</option>
                  {users.map((user) => {
                    return <option value={user._id}>{user.name}</option>;
                  })}
                </Form.Control>
                <Form.Control.Feedback type="invalid">
                  {errors.assignedMobitelOfficer}
                </Form.Control.Feedback>
              </Form.Group>
            </Row>

            <Row className="mb-3">
              <Form.Group as={Col}>
                <Form.Label>Project Name</Form.Label>
                <Form.Control
                  disabled
                  onChange={(e) => setField("project", e.target.value)}
                  value={form.project}
                  className="col-md-9"
                  type="text"
                  placeholder="Enter Project Name"
                  isInvalid={!!errors.project}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.project}
                </Form.Control.Feedback>
              </Form.Group>

              <Form.Group as={Col}>
                <Form.Label>Sub-Project Name</Form.Label>
                <Form.Control
                  disabled
                  onChange={(e) => setField("subProject", e.target.value)}
                  value={form.subProject}
                  className="col-md-9"
                  type="text"
                  placeholder="Enter Sub-Project Name"
                  isInvalid={!!errors.subProject}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.subProject}
                </Form.Control.Feedback>
              </Form.Group>
            </Row>
            <Modal.Footer>
              <Button variant="contained" color="primary" type="submit">
                Save
              </Button>
            </Modal.Footer>
          </Form>
        </Modal.Body>
      </Modal>
      <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={() => setOpen(false)}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert
          onClose={() => setOpen(false)}
          severity={severity}
          sx={{ width: "100%" }}
        >
          {alart}
        </Alert>
      </Snackbar>
      <Dialog
        open={openDialog}
        onClose={handleCloseLoading}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withRouter(UpdateProjectTemplateBody);
